













import { Component, Mixins } from "vue-property-decorator";
import tagHelper from "@/mixins/tagHelper";
@Component
export default class extends Mixins(tagHelper) {
  visible = false;
  tagName = "";
  handleOk() {
    this.createTag(this.tagName);
    this.hide();
  }
  show() {
    this.tagName = "";
    this.visible = true;
  }
  hide() {
    this.visible = false;
  }
}
