



























import Vue from "vue";
import { Component, Watch } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import recordTypeList from "@/constants/recordTypeList";
import intervalList from "@/constants/intervalList";
import dayjs from "dayjs";
import clone from "@/lib/clone";
import ECharts from "vue-echarts/components/ECharts.vue";
import "echarts/lib/chart/bar";

@Component({
  components: { Tabs, "v-chart": ECharts },
})
export default class extends Vue {
  type = "-";
  showChart = false;
  chartOption = {};
  recordTypeList = recordTypeList;
  recordDataSource = recordTypeList;
  intervalDataSource = intervalList;
  @Watch("type", { immediate: true })
  onTypeChanged(val: string, oldVal: string) {
    const xAxisData = <any>[];
    const seriesData = <any>[];
    const _chartData = <any>{};
    this.recordList.forEach((record: RecordItem) => {
      if (record.type === this.type) {
        const tagName = record.tags[0].name;
        const amount = (_chartData[tagName] || 0) + record.amount;
        _chartData[tagName] = amount;
      }
    });

    this.showChart = Object.keys(_chartData).length > 0;
    this.chartOption = {
      title: {
        text: "ECharts 入门示例",
        show: true,
      },
      tooltip: {},
      legend: {
        data: ["销量"],
      },
      calculable: true,
      grid: {
        top: "40px",
        left: "1%",
        right: "10%",
        bottom: "20px",
        containLabel: true,
      },
      yAxis: [
        {
          type: "value",
          name: "收入(元)",
        },
      ],
      xAxis: {
        data: Object.keys(_chartData),
      },
      series: [
        {
          name: "销量",
          type: "bar",
          barWidth: 30,
          data: Object.values(_chartData),
        },
      ],
    };
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {
    const { recordList } = this;
    if (recordList.length === 0) {
      return [];
    }

    const newList = clone(recordList)
      .filter((r) => {
        return r.type === this.type;
      })
      .sort(
        (a, b) =>
          dayjs(b.createdDate.toString()).valueOf() -
          dayjs(a.createdDate.toString()).valueOf()
      );
    if (newList.length === 0) {
      return [];
    }
    type Result = { title: string; total?: number; items: RecordItem[] }[];
    const result: Result = [
      {
        title: dayjs(newList[0].createdDate.toString()).format("YYYY-MM-DD"),
        items: [newList[0]],
      },
    ];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (
        dayjs(last.title).isSame(dayjs(current.createdDate.toString()), "day")
      ) {
        last.items.push(current);
      } else {
        result.push({
          title: dayjs(current.createdDate.toString()).format("YYYY-MM-DD"),
          items: [current],
        });
      }
    }
    result.map((group) => {
      group.total = group.items.reduce((sum, item) => {
        return sum + item.amount;
      }, 0);
    });
    return result;
  }
  tagString(tags: Tag[]) {
    return tags.length === 0 ? "无" : tags.map((v) => v.name).join(",");
  }
  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, "day")) {
      return "今天";
    } else if (day.isSame(now.subtract(1, "day"), "day")) {
      return "昨天";
    } else if (day.isSame(now.subtract(2, "day"), "day")) {
      return "前天";
    } else if (day.isSame(now, "year")) {
      return day.format("M月D日");
    } else {
      return day.format("YYYY年M月D日");
    }
  }

  beforeCreate() {
    this.$store.commit("fetchRecord");
  }
}
