




















import Vue from "vue";
import { Component, Ref } from "vue-property-decorator";
import Tags from "@/components/Money/Tags.vue";
import Tabs from "@/components/Tabs.vue";
import NumberPad from "@/components/Money/NumberPad.vue";
import recordTypeList from "@/constants/recordTypeList";
import moment from "moment";
import { DatePicker } from "ant-design-vue";
import { DatepickerCommon } from "ant-design-vue/types/date-picker/common";

window.localStorage.setItem("version", "0.0.1");

@Component({
  components: { Tags, Tabs, NumberPad },
})
export default class extends Vue {
  @Ref() readonly numberPad!: NumberPad;
  tabDataSource = recordTypeList;
  record: RecordItem = {
    tags: [],
    notes: "",
    type: "-",
    amount: 0,
    createdDate: moment(new Date()),
  };
  recordList = this.$store.state.recordList;
  created() {
    this.$store.commit("fetchRecord");
  }
  submit() {
    if (!this.record.tags || this.record.tags.length === 0) {
      return this.$message.warning("请选择标签", 1);
    }
    if (!this.record.amount || this.record.amount === 0) {
      return this.$message.warning("请输入金额", 1);
    }
    this.$store.commit("createRecord", this.record);
    const msg = this.$store.state.createTagError;
    msg ? this.$message.warning(msg, 1) : this.$message.success("添加成功", 1);
    this.reset();
  }
  onSelectedTags(selectedTags: Tag[]) {
    this.record.tags = selectedTags;
  }
  reset() {
    Object.assign(this.record, {
      notes: "",
      amount: 0,
    });
    this.numberPad.clear();
  }
}
